$primaryfontcolor: #192A44;
$secondarycolor: #469BFF;
$mainfont: "Inter", serif;

.landingpage_nav.light{
    // background: transparent;
    padding-top: 5rem;
    margin-left: 10rem;
    // border: 1px solid black;
    position: relative;
    width: 85%;

    .Users{
        position: absolute;
        right: 7.5rem;
        bottom: -1.5rem;
        gap: 1rem;

        .alt{
            background-color: $secondarycolor;
            border-bottom-right-radius: 0rem;
            border-bottom-left-radius: 3rem;
        }

        li{
            background-color: $primaryfontcolor;
            color: white;
            width: 130px;
            text-align: center;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 3rem;
            border-bottom-right-radius: 0rem;
        }
    }

    a{
        text-decoration: none;
    }
    ul{
    // background: transparent;
        margin: 0;
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        width: fit-content;
        flex-direction: row;
        li{
            color: $primaryfontcolor;
            font-family: $mainfont;
            span{
                font-weight: 700;
                font-size: 2rem;
            }
            
        }
    }
}

.landingpage_nav.dark{
    // background: transparent;
    padding-top: 5rem;
    margin-left: 10rem;
    position: relative;
    width: 85%;

    .Users{
        position: absolute;
        right: 7.5rem;
        bottom: -1.5rem;
        gap: 1rem;

        .alt{
            background-color: #EEBD19;
            color: white;
            border-bottom-right-radius: 0rem;
            border-bottom-left-radius: 3rem;
        }

        li{
            background-color: $secondarycolor;
            color: white;
            width: 130px;
            text-align: center;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 3rem;
            border-bottom-right-radius: 0rem;
        }
    }

    a{
        text-decoration: none;
        color: white;
    }
    ul{
    // background: transparent;
        margin: 0;
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        width: fit-content;
        flex-direction: row;
        li{
            color: white;
            font-family: $mainfont;
            span{
                font-weight: 700;
                font-size: 2rem;
                color: white;
            }
            
        }
    }
}