$primary-bg: #192a44;
$light-text: #f3f4f6;
$dark-text: white;
$accent-color: #959795;
$light-accent: #9ca3af;
$gap-size: 1.5rem;
$logo-size: 160px;
$logo-height: 80px;

.gallery-main.light{
    height: 100vh;
    width: 100%;
    font-family: "Inter";



    .gallery-frame{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;


        .gallery-top{
            width:80%;
            // border: 2px solid red;
            height: fit-content;


            .gallery-top-frame{
                display: flex;
                flex-direction: column;
                


                aside{
                    display: flex;

                    &:nth-of-type(1){
                        display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            gap: 10px;
            height: fit-content;
            width: fit-content;
                            img{
                                width: 15px;
                                height: 15px;
                                object-fit: cover;
                            }
                            p{
                                font-family: "Inter";
                                font-weight: 700;
                                font-size: 0.8rem;
                                padding: 0;
                                margin: 0;
                                line-height: 24px;
                                letter-spacing: 0.76px;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                
                            
                            }
                        
                    }
                    &:nth-of-type(2){
                        justify-content: space-between;
                        // border: 2px solid red;

                        div{
                            width: 100%;
                        }

                        .top-left{
                            h1{
                                font-size: 3rem;
                                margin: 0;
                                color: #666666;
                            }
                        }

                        .top-right{
                            p{
                            font-size: 1.2rem;
                            margin: 0;
                            color: #060E1F;
                            margin-top: 0.7rem;

                            }
                        }

                        
                    }
                }
            }

        }
        .gallery-bottom{
            width: 100%;
            // border: 2px solid blue;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

        }
    }
}
