.gallery-section {
  position: relative;
  background-color: $primary-bg;
  overflow: hidden;
  padding: 4rem 2rem;
  margin-top: 4rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 80%;
    transform: translateX(-50%);
    width: 40%;
    height: 40%;
    background: url("https://i.imgur.com/u2ADltt.png") no-repeat center center;
    opacity: 1;
    z-index: 0;
  }

  &.dark {
    color: $dark-text;
  }

  &.light {
    color: $light-text;
  }
}

.gallery-header {
  text-align: left;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 1;

  h3 {
    font-size: 1.15rem;
    font-weight: 300;
    color: $accent-color;
  }

  h2 {
    font-size: 2.8rem;
    font-weight: 700;
    margin: 0.25rem 0 0.8rem;
    color: $dark-text;

    .light & {
      color: #c9e0e0;
    }
  }

  p {
    font-size: 20px;
    font-weight: 300;
    color: $accent-color;

    .light & {
      color: $light-accent;
    }
  }
}

.gallery-rows-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: $gap-size;
  position: relative;
  z-index: 1;
  height: 10vh;
}

.gallery-row-wrapper {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
//   border: 2px solid green;
    height:40%;
}

.gallery-row {
  display: flex;
  gap: 50px;
  min-width: max-content;
}

.gallery-logo {
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: $logo-size;
  height: $logo-height;

  &.dark {
    background-color: #666666;
  }

  .light & {
    background-color: #060e1f;
  }
}

.gallery-image {
  object-fit: contain;
  img{
    height: 250px;
    width: 350px;
    border-radius: 1rem;
    object-fit: cover;
  }
}

