$primaryfontcolor: #192A44;
$secondarycolor: #469BFF;
$mainfont: "Inter", serif;

.banner_main.light{
    background: transparent;
    width: 100%;
    height: 100vh;
    // border: 1px solid red;
    display: flex;
    aside{
        width: 100%;
        height: 100%;
        // border: 5px solid green;
        &:nth-of-type(1){
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            position: relative;

            .banner_text{
                // width: 100%;
                position: absolute;
                left: 12rem;
                top: 0rem;
                h1{
                    font-size: 5.5rem;
                    font-family: $mainfont;
                    font-weight: 600;
                    margin-bottom: 0;
                    color: $primaryfontcolor;
                }
                p{
                    // margin-top: 0;
                    font-family: $mainfont;
                    color: $primaryfontcolor;
                    margin-left: 1rem;
                }
                h3{
                    font-family: $mainfont;
                    color: $primaryfontcolor;
                    margin-left: 1.5rem;
                }
                .landingpage_buttons{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 3rem;
                    margin-top: 2.5rem;
        
                    button{
                        border: transparent;
                    font-family: $mainfont;
                    cursor: pointer;
                        &:nth-of-type(1){
                            background-color: $secondarycolor;
                            border-bottom-left-radius: 0;
                        }
                        &:nth-of-type(2){
                            background-color: $primaryfontcolor;
                            border-bottom-right-radius: 0;
                        }
                        color: white;
                        padding: 1rem;
                        border-radius: 4rem;
                        width: 150px;
                    }
                }
                h3{
                    margin-top: 3rem;
                    margin-left: 1.5rem;

                }
                .landingpage_list{
                    margin-left: 1.5rem;
                    display: flex;
                    gap: 5rem;
                    ul{
                    list-style: none;
                    // margin: 0;
                    padding: 0;
                    color: $primaryfontcolor;
                    font-family: $mainfont;
                    display: flex;
                    flex-direction: column;
                    row-gap: 0.7rem;
                    }
                }
            }

        }

        &:nth-of-type(2){
            position: relative;
            width: 60%;
            // border: 5px solid black;
            .landingpage_banner{
                display: flex;
                // border: 5px solid black;
                width: fit-content;
                position: relative;
                width: 500px;
                justify-content: center;
                align-items: center;
                height: 100%;
                position: absolute;
                left: -5rem;
                top: -7.5rem;



                .one{
                    width: 150px;
                    height: 400px;
                    border-radius: 4rem;
                    // border: 1px solid black;
                    background-image: url(https://s3-alpha-sig.figma.com/img/76a1/6709/a2a831fe0fe9a80326377c292cdbfe2e?Expires=1740960000&Key-Pair-Id=APKAQ4GOSFWCW27IBOMQ&Signature=n1RjI4sx6~MM8xJ0grqCxP2HJUjtDEY9WcJ2H8uYiTV4EJL1c8an4ZPtE2SrEQ26y4F6srATSPdf-pUqRk0tXN~XybRS04mwTFDWj2xGlpxT81rnmyQbxxkE0jE~40~lfU80DL7dhmjadSmZjdO01Btzmg-emzND6xuTwud6LZKqu6i3Z6McA691OVtwCaBaomzEqqVMrgvekBX5C7SXapvcRNkwwT43oRCapzutFTsJmnhb8GtwNy0332p8YY6pMIP4pphkFiFD7A0FfdRUqllSUp2iP5R-Vb4uxfx2q-VbwEYA6XFC445DhrS8pvA9QlqySXC6pLpxbMGBHmBNYg__);
                    background-position: center top;
                    background-size: cover;
                    position: absolute;
                    left: 0;
                }
                .circle{
                    position: absolute;
                    height: 43px;
                    width: 43px;
                    clip-path: circle();
                    &:nth-of-type(4){
                        background-color:#EEBD19;
                        left: 7rem;
                        bottom: 10rem;
                        animation: wobble 3s ease-in-out infinite;
                        }
                    &:nth-of-type(2){
                        background-color:#F04F5A;
                        right: 7rem;
                        top: 10rem;
                        animation: wobble 3s ease-in-out infinite;
                        }
                    &:nth-of-type(3){
                        background-color:#2688FC;
                        right: 0rem;
                        bottom: 9rem;
                        animation: wobble 3s ease-in-out infinite;
                        }
                }
                .two{
                    display: flex;
                    flex-direction: column;
                    row-gap: 2rem;
                    .top{
                        width: 150px;
                        height: 400px;
                        border-radius: 4rem;
                        // border: 1px solid black;
                        background-image: url(https://s3-alpha-sig.figma.com/img/3187/aee9/3b26e72b8ef0eb62323b57f2c94ada77?Expires=1740960000&Key-Pair-Id=APKAQ4GOSFWCW27IBOMQ&Signature=JP8qUqCHjoEW8CwLW3vRKzPrKExsZOd3-NsQbnjnT84HGlL4xeajtrR5goeInIsM7TSx6caNjnuYuhn3svPs21mypiXtWIBBDEyyQCbKbL29vuCfC70KaAaLw9mXkY4hapIiABeak2vMiua7-gPj8ySQ6ORtEqUi~6cIY2BpwERsu1tPD8vbCKfnyIrV99CTT6Ii7xvQ9lXZ0AB09Q81JE1h51s5g9mMHkWBDyFLYXpoJ9qBn99tQsJ4B436ognP9h85JLqWP8918ZZprMYBB1wv~4aoV4L0F2RcVdWz58XLgfMXdCdVpO2e898t5qzLVIWUaE7XyLzoJOmj-ncY6A__);
                        background-position: center top;
                        background-size: cover;
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;
                    }
                    .bottom{
                        width: 150px;
                        height: 400px;
                        border-radius: 4rem;
                        // border: 1px solid black;
                        background-image: url(https://s3-alpha-sig.figma.com/img/534a/ab05/852ac2546d87081b636eb52bb85b2a47?Expires=1740960000&Key-Pair-Id=APKAQ4GOSFWCW27IBOMQ&Signature=cJcdcNRYcPfqOlMQUgBPf5gzUjGgeotrIITnUOdWcN~K~LcP4DCqc3~KhR4izETPXv07PRhq02W-NESYntncHrwXF~-zSigkOfTLf-xJIsgXjaWsxfXVwdRfTtQRTnqoGn6105~EDQDRnzNPoozU~NbfLq19b1Joctaxs7c~w3OPazWa5mpjGlapGR67v-uBcMOwzxWLC5PtcXgmbibO7OTtn1bYJB0E5Q8mYjQGYn3KmZ4C3d21yCJNB-iJGo0Hf503OsJG4IQ7KRiJu2dXZCeTma5xjEzXODeoFCqPi3C3YTkyz~8bg-82xqGWIpRL03SIrXaFyYY1Qzh~NyEGmA__);
