                        background-size: cover;
                    }
                }

                .three{
                    width: 150px;
                    height: 400px;
                    border-radius: 4rem;
                    // border: 1px solid black;
                    background-image: url(https://s3-alpha-sig.figma.com/img/30aa/473e/e21ae3eb7e3b8c79f5ecfb8173b840a3?Expires=1740960000&Key-Pair-Id=APKAQ4GOSFWCW27IBOMQ&Signature=ErUY9V1jxd97scYgtlXb3SQ2uJn8TCCmwsAooVGPGzbmnuUgfrfponh-0z4RgHSsqWcaNW7hbXkxad940O4HG4MGOuVXoEp50y6PLS9XAdg24zWNrOgCO8XQH5SnoiKwzab-L3yuZn6h8WaUC-mtXgI2ZrCkjITzGf4-cY6hIJdGkKSunhLxq6dElXlS1IrUdsfHOs9aQIXjkMb9heQI2qkYdLSIf~v87Cyamb~NKRZXCcrMu47oOSLo2tLCqzvoe1DsyUOUbtUObv3Ls2qGSL243lUEEsJZuZNZG3eaxlzi6epy0Jcw5y7AzNhy~MhLhkMe9RH2JKR~6cq2yX6YmA__);
                    background-position: center;
                    background-size: 200% 150%;
                    background-repeat: no-repeat;
                    position: absolute;
                    right: 0;
                }
            }
        }
    }


}

