.landingpage_main.light{
    height: fit-content;
    width: 100%;
    background: url('') no-repeat center center/cover;
    // overflow-x: hidden;
    position: relative;
    // transition: all 0.3s ease-in-out;
    // z-index: 1;
    // background: linear-gradient(135deg, rgba(111, 116, 224, 0.356), rgba(0, 0, 0, 0.1));
    .Ellipse{
        position: absolute;
        width: 822px;
        height: 822px;
        filter: blur(300px);
        z-index: 0;

        &:nth-of-type(1){
            top: -473px;
            left: -473px;
            background: #4D6FB8; /* Light background */
        }
        &:nth-of-type(2){
            top: 584px;
            left: 1482px;
            background: #9F60C6; /* Light background */
        }
    }


    .toggle{
        width: 180px;
        border-radius: 3rem;
        border: 3px solid #4F378B;
        display: flex;
        height: 3rem;
        position: absolute;
        top: 50rem;
        right: 12rem;
        font-family: $mainfont;
        // overflow: hidden;
        button{
            width: 100%;
            background-color: transparent;
            border: transparent;
            cursor: pointer;
            &:nth-of-type(1){
                border-right: 1px solid #4F378B;
                color: #79747E;
                border-top-left-radius: 3rem;
                border-bottom-left-radius: 3rem;
                // border: 1px solid #4F378B;
            }
            &:nth-of-type(2){
                background-color: #4F378B;
                color: white;
                border-top-right-radius: 3rem;
                border-bottom-right-radius: 3rem;
                // border: 1px solid #4F378B;

            }
        }
    }


}

