.cards_main.light{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    font-family: "Inter";

    .cards_frame{
        width: 80%;
    }

    .cards_program{
        display: flex;
        flex-direction: column;
        gap: 10px;


        .cards_tag{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            gap: 10px;
            height: fit-content;
            width: fit-content;
            img{
                width: 15px;
                height: 15px;
                object-fit: cover;
            }
        
            p{
                font-family: "Inter";
                font-weight: 300;
                font-size: 0.8rem;
                padding: 0;
                margin: 0;
                line-height: 24px;
                letter-spacing: 0.76px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

            
            }
        }
   
    }

    h1{
        font-size: 56.8px;
        font-weight: 600;
        line-height: 61.6px;
        letter-spacing: -2.66px;
        margin-top: 0;
    }


    .cards_division{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        gap: 3rem;

        .cards_alignment{
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;
            gap: 3rem;
            width: 20rem;
            height: 30rem;
            overflow: hidden;


            .cards_sect{
                background-color: #333333;
                height: 100%;
                width: 100%;
                color: white;
                padding: 40px;
                padding-top: 100px;

                img{
                    width: 85px;
                    height: 85px;
                    object-fit: contain;
                }

                #title{
                    font-size: 2rem;
                    font-weight: 600;
                    margin-bottom: 0;
                    
                }
                p{
                    font-size: 20.5px;
                    

                }
            }
        }
        
    }

   
}

