.landingpage_nav.light {
  padding-top: 5rem;
  margin-left: 10rem;
  position: relative;
  width: 85%;
}
.landingpage_nav.light .Users {
  position: absolute;
  right: 7.5rem;
  bottom: -1.5rem;
  gap: 1rem;
}
.landingpage_nav.light .Users .alt {
  background-color: #469BFF;
  border-bottom-right-radius: 0rem;
  border-bottom-left-radius: 3rem;
}
.landingpage_nav.light .Users li {
  background-color: #192A44;
  color: white;
  width: 130px;
  text-align: center;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3rem;
  border-bottom-right-radius: 0rem;
}
.landingpage_nav.light a {
  text-decoration: none;
}
.landingpage_nav.light ul {
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  width: -moz-fit-content;
  width: fit-content;
  flex-direction: row;
}
.landingpage_nav.light ul li {
  color: #192A44;
  font-family: "Inter", serif;
}
.landingpage_nav.light ul li span {
  font-weight: 700;
  font-size: 2rem;
}

